.home-main-section{
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    flex-grow: 1;
}

.home-main-section .img-wrapper{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: stretch;
    justify-content: end;
}

.home-main-section .lady-image{
    background: url('../img/landing-page-girl.png');
    flex-grow: 1;
    min-width: 30vw;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: bottom left;
}

.home-main-section .call-to-action{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 1rem;
    align-self: center;
}

.home-page-circle-1{
    position: absolute;
    width: 25vw;
    height: 25vw;
    border-radius: 50%;
    background-color: rgba(188, 58, 128, 10%);
    bottom: 5vw;
    left: 5vw;
    z-index: -1;
}


.home-page-circle-2{
    position: absolute;
    width: 15vw;
    height: 15vw;
    border-radius: 50%;
    background-color: rgba(23, 98, 167, 10%);
    top: 15vh;
    left: 30vw;
    z-index: -1;
}


.home-page-circle-3{
    position: absolute;
    overflow: hidden;
    width: 45vw;
    height: 45vw;
    bottom: 0;
    right: 0;
    z-index: -1;
}

.home-page-circle-3::before{
    content: '';
    position: absolute;
    transform: translate(50%, 50%);
    height: 100%;
    width: 100%;
    border-radius: 50%;
    background-color: rgba(23, 98, 167, 10%);

}

@media(max-width: 900px){
    .home-main-section .img-wrapper{
        display: none;
    }

    .home-main-section{
        justify-content: center;
    }
}